*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
}
.navbar{
    width: 100%;
    height: 3.5em;
    display: flex;
    justify-content: center;
    background-color: #4070f4;
}
.navitems{
    width:70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}
.navitems h1{
    color:  white;
}
.navitems h1:hover{
    transform: scale(0.98);
    cursor: pointer;
}
.navitems button{
    width: 80px;
    height: 35px;
    color: white;
    background-color: #4070f4;
    text-decoration: none;
    border:1.5px solid white;
    border-radius: 2px;
} 
.mainDiv{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    height: 90vh;
    background-color: #4070f4;
}
.left{
    width: 550px;
    height: 400px;
    font-size: 1.2em;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    margin-top: 10px;
    color: white;
}
.header{
    font-size: 45px;
    margin-top: 70px;
}
.header p{
    font-weight:100;
}
.right
{
    width: 400px;
    height: 400px;
    margin-left: 60px;
    margin-top: 10px;
    margin-left: 20px;
    background-color: #4649FF;
    border-radius: 76% 24% 82% 18% / 49% 63% 37% 51% ;
    text-align: center;
    animation: borderAni 6s linear infinite;
}
.right img{
    width: 90%;
    height: 90%;
}
.footer{
    width: 100%;
    height: 3.5em;
    display: flex;
    justify-content: start;
    align-items: center;
    background-color: #4649FF;
    left: 0;
    bottom: 0;
    color: white;
}
.footer p{
    margin-left: 20px;
}
@keyframes borderAni {
    0%, 100% {
        border-radius: 76% 24% 82% 18% / 49% 63% 37% 51%;
    }
    75%{
        border-radius:75% 25% 75% 25% / 23% 80% 20% 77% 
    }
    50% {
        border-radius: 86% 14% 92% 8% / 38% 78% 22% 62%;
    }
  }